import { FormlyFieldConfig } from '@ngx-formly/core';
import { ApplicantIncome } from '../model/CheckListData';
import { TranslateService } from '@ngx-translate/core';
import { Injectable } from '@angular/core';
import { StepType } from '@app/modules/shared-module/model';

@Injectable({
  providedIn: 'root'
})
export class ChecklistFormConfigurationService {
  steps: StepType[] = [];
  constructor(private translations: TranslateService) {
    this.steps = [
      {
        label: translations.instant('Checklist.step1.label'),
        headerText: translations.instant('Checklist.step1.headerText'),
        fields: this.formFieldStepOne()
      },
      {
        label: translations.instant('Checklist.step2.label'),
        headerText: translations.instant('Checklist.step2.headerText'),
        fields: this.formFieldStepTwo()
      },
      {
        label: translations.instant('Checklist.step3.label'),
        headerText: translations.instant('Checklist.step3.headerText'),
        fields: this.formFieldStepThree()
      },
      {
        label: translations.instant('Checklist.step4.label'),
        headerText: translations.instant('Checklist.step4.headerText'),
        fields: this.formFieldStepFour()
      }
    ];
  }


  formFieldStepOne(): FormlyFieldConfig[] {
    return [
      {
        fieldGroup: [
          this.formFiledCreditAmount()
        ]
      }
    ];
  }

  formFieldStepTwo(): FormlyFieldConfig[] {
    return [
      {
        key: 'CheckListData',
        fieldGroup: [
          {
            key: 'creditPurposes',
            type: 'repeat-section',
            templateOptions: {
              header: 'Cel kredytu ',
              mainHeaderSuffix: '(podstawowy)',
              nextmainHeaderSuffix: '(dodatkowy)',
              sectionIcon: 'app-icon app-credit-standing',
              addBtnText: 'Dodaj kolejny cel kredytu',
              deleteBtnText: 'Usu cel kredytu',
              className: 'app-section',
              max: 4,
            },
            fieldArray: {
              fieldGroup: [
                this.formCreditPurpose(),
                this.formFlatPurchaseType(),
                this.formHousePurchaseType(),
                this.formPropertyState(),
                this.formRefinancingPurpose(),
                this.formRefinancingType()
              ],
            },
          },
        ]
      }
    ];
  }

  formFieldStepThree(): FormlyFieldConfig[] {
    return [
      {
        key: 'CheckListData',
        fieldGroup: [
          {
            className: '',
            key: 'applicants',
            type: 'repeat-section',
            templateOptions: {
              header: 'Wnioskujcy ',
              mainHeaderSuffix: '(gwny)',
              nextmainHeaderSuffix: '(kolejny)',
              sectionIcon: 'app-icon app-credit-applicant',
              addBtnText: 'Dodaj kolejnego wnioskujcego',
              deleteBtnText: 'Usu wnioskujcego',
              className: 'app-section',
              max: 4,
            },
            fieldArray: {
              fieldGroup: [
                {
                  className: 'col-12',
                  wrappers: ['form-field-vertical'],
                  type: 'custom-radio',
                  key: 'maritalStatus',
                  templateOptions: {
                    required: true,
                    label: 'Stan cywilny',
                    options: [
                      { value: 'unmarried', label: 'panna / kawaler', tooltip: '', scrollBy: 120  },
                      { value: 'married', label: 'zamna / onaty', tooltip: '', scrollBy: 120  },
                      { value: 'widower', label: 'wdowa / wdowiec', tooltip: '', scrollBy: 120  },
                      { value: 'separation', label: 'separacja', tooltip: '', scrollBy: 120  },
                      { value: 'divorced', label: 'rozwiedziona / rozwiedziony', tooltip: '', scrollBy: 120  }
                    ],
                    description: '',
                    attributes: {
                      tooltip: ''
                    },
                    groupName: 'maritalStatus'
                  }
                },
                {
                  className: 'col-12',
                  wrappers: ['form-field-vertical'],
                  type: 'custom-radio',
                  key: 'regimeOfSeparateProperty',
                  templateOptions: {
                    required: true,
                    label: 'Rozdzielno majtkowa',
                    options: [
                      { value: 'separationProperty', label: 'zostaa ustanowiona', tooltip: '' },
                      { value: 'jointProperty', label: 'nie zostaa ustanowiona', tooltip: '' }
                    ],
                    description: '',
                    attributes: {
                      tooltip: ''
                    },
                    groupName: 'regimeOfSeparateProperty'
                  },
                  hideExpression: ($viewValue, $modelValue, scope) => {
                    const hide = $viewValue.maritalStatus !== 'married';
                    if (hide) {
                      $viewValue.regimeOfSeparateProperty = undefined;
                    }
                    return hide;
                  }
                },
                {
                  className: 'Income',
                  key: 'incomes',
                  type: 'repeat-section',
                  defaultValue: [new ApplicantIncome()],
                  templateOptions: {
                    header: '',
                    mainHeaderSuffix: '',
                    nextmainHeaderSuffix: '',
                    sectionIcon: '',
                    addBtnText: 'Dodaj kolejne rdo dochodu',
                    deleteBtnText: 'Usu rdo dochodu',
                    className: '',
                    max: 4,
                  },
                  fieldArray: {
                    fieldGroup: [
                      {
                        className: '',
                        wrappers: ['form-field-vertical'],
                        type: 'custom-radio',
                        key: 'incomeType',
                        templateOptions: {
                          required: true,
                          label: 'rdo dochodu',
                          options: [
                            { value: 'employmentContract', label: 'umowa o prac', tooltip: '', scrollBy: 120  },
                            {
                              value: 'B2B',
                              label: 'dziaano gospodarcza',
                              tooltip: 'Minimalny okres  prowadzenia dziaalnoci gospodarczej to 12 miesicy,' +
                                ' lub 3 miesice jeli nastpia  zmiana formy zatrudnienia z umowy o prac na dziaalno gospodarcz',
                              scrollBy: 120
                            },
                            {
                              value: 'orderContract',
                              label: 'umowa o dzieo / zlecenie',
                              tooltip: 'Akceptujemy dochd jeli uzyskujesz go od minimum 12 miesicy'
                            },
                            {
                              value: 'farmIncome',
                              label: 'gospodarstwo rolne',
                              tooltip: 'Akceptujemy dochd jeli uzyskujesz go od minimum 12 miesicy',
                              scrollBy: 120
                            },
                            { value: 'otherIncome', label: 'inne rda dochodu', tooltip: '', scrollBy: 120  },
                            { value: 'noIncome', label: 'brak dochodu', tooltip: '' }
                          ],
                          description: '',
                          attributes: {
                            tooltip: ''
                          },
                          groupName: 'incomeType'
                        }
                      },
                      {
                        className: '',
                        wrappers: ['form-field-vertical'],
                        type: 'custom-radio',
                        key: 'employmentContractType',
                        templateOptions: {
                          required: true,
                          label: 'Umowa o prac zawarta jest na',
                          options: [
                            {
                              value: 'noTermContract', label: 'czas nieokrelony',
                              tooltip: 'Musisz by zatrudniony u aktualnego pracodawcy od minimum 3 miesicy',
                              scrollBy: 120
                            },
                            {
                              value: 'fixedTermContract', label: 'czas okrelony',
                              tooltip: 'Akceptujemy dochd  jeli uzyskujesz go od 3 miesicy oraz: (1) ' +
                                'jest to kolejna umowa z tym samym pracodawc,  lub (2) umowa obowizuje od minimum  5 lat' +
                                ' oraz  6 miesicy do przodu, lub (3) umowa obowizuje od ' +
                                ' minimum 6 m-cy z danym pracodawc i zawarta na min. 6 m-cy do przodu oraz udukomentujesz  ' +
                                'cigo zatrudnienia od min. 12 m-cy, lub (4) charakter umowy wynika ze specyfiki zatrudniania' +
                                '  w  grupie  zawodowej takiej jak: nauczyciele, lekarze, pracownicy sub mundurowych.',
                              scrollBy: 120
                            },
                          ],
                          description: '',
                          attributes: {
                            tooltip: ''
                          },
                          groupName: 'employmentContractType'
                        },
                        hideExpression: ($viewValue, $modelValue, scope) => {
                          const hide = $viewValue.incomeType !== 'employmentContract';
                          if (hide) {
                            $viewValue.employmentContractType = undefined;
                          }
                          return hide;
                        }
                      },
                      {
                        className: 'app-checkbox-group-label',
                        template: '<div>Informacje dodatkowe:</div>',
                        hideExpression: 'model.incomeType !== "employmentContract"',
                      },
                      {
                        className: 'app-checkbox-group-list',
                        hideExpression: ($viewValue, $modelValue, scope) => {
                          const hide = $viewValue.incomeType !== 'employmentContract';
                          if (hide) {
                            $viewValue.employmentIncomeTypeBankAccount = false;
                            $viewValue.employmentIncomeTypePremium = false;
                            $viewValue.employmentIncomeTypeInternationalIncome = false;
                          }
                          return hide;
                        },
                        fieldGroup: [
                          {
                            className: 'app-checkbox',
                            key: 'employmentIncomeTypePremium',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'wynagrodzenie wypacane jest w formie gotwkowej',
                              description: '',
                              attributes: {
                                tooltip: ''
                              },
                            }
                          },
                          {
                            className: 'app-checkbox',
                            key: 'employmentIncomeTypeBankAccount',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'wynagrodzenie powikszone jest o premi',
                              description: '',
                              attributes: {
                                tooltip: ''
                              },
                            }
                          },
                          {
                            className: 'app-checkbox',
                            key: 'employmentIncomeTypeInternationalIncome',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'dochd jest uzyskiwany za granic Polski',
                              description: '',
                              attributes: {
                                tooltip: ''
                              },
                            }
                          }
                        ]
                      },
                      {
                        className: '',
                        wrappers: ['form-field-vertical'],
                        type: 'custom-radio',
                        key: 'b2bContractType',
                        templateOptions: {
                          required: true,
                          label: 'Dziaalno prowadzona jest w formie',
                          options: [
                            { value: 'soleTrader', label: 'jednoosobowej dziaalnoci gospodarczej', tooltip: '' },
                            { value: 'partnership', label: 'spki cywilnej', tooltip: '' },
                            { value: 'generalPartnership', label: 'spki jawnej lub partnerskiej', tooltip: '' },
                          ],
                          description: '',
                          attributes: {
                            tooltip: ''
                          },
                          groupName: 'b2bContractType'
                        },
                        hideExpression: ($viewValue, $modelValue, scope) => {
                          const hide = $viewValue.incomeType !== 'B2B';
                          if (hide) {
                            $viewValue.b2bContractType = undefined;
                          }
                          return hide;
                        }
                      },
                      {
                        className: '',
                        wrappers: ['form-field-vertical'],
                        type: 'custom-radio',
                        key: 'b2bContractBillingType',
                        templateOptions: {
                          required: true,
                          label: 'Rozliczenie nastpuje na podstawie',
                          options: [
                            { value: 'revenueExpenseLedger', label: 'KPiR', tooltip: '' },
                            { value: 'flatRate', label: 'ryczatu', tooltip: '' },
                            { value: 'integratedAccounting', label: 'penej ksigowoci', tooltip: '' },
                            { value: 'taxCard', label: 'karty podatkowej', tooltip: '' },
                          ],
                          description: '',
                          attributes: {
                            tooltip: ''
                          },
                          groupName: 'b2bContractBillingType'
                        },
                        hideExpression: ($viewValue, $modelValue, scope) => {
                          const hide = $viewValue.incomeType !== 'B2B';
                          if (hide) {
                            $viewValue.b2bContractBillingType = undefined;
                          }
                          return hide;
                        }
                      },
                      {
                        className: 'app-checkbox-group-label',
                        template: '<div>Informacje o gospodarstwie:</div>',
                        hideExpression: 'model.incomeType !== "farmIncome"',
                      },
                      {
                        className: 'app-checkbox-group-list',
                        hideExpression: ($viewValue, $modelValue, scope) => {
                          const hide = $viewValue.incomeType !== 'farmIncome';
                          if (hide) {
                            $viewValue.farmIncomeTypeSpecialBranches = false;
                            $viewValue.farmIncomeTypeRecordKeeping = false;
                            $viewValue.farmIncomeTypeTenancy = false;
                          }
                          return hide;
                        },
                        fieldGroup: [
                          {
                            className: 'app-checkbox',
                            key: 'farmIncomeTypeSpecialBranches',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'produkcja w dziaach specjalnych produkcji rolnej',
                              description: '',
                              attributes: {
                                tooltip: ''
                              },
                            }
                          },
                          {
                            className: 'app-checkbox',
                            key: 'farmIncomeTypeRecordKeeping',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'ksigowo w formie ksig handlowych',
                              description: '',
                              attributes: {
                                tooltip: ''
                              },
                            }
                          },
                          {
                            className: 'app-checkbox',
                            key: 'farmIncomeTypeTenancy',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'cz gruntw do produkcji jest dzierawiona',
                              description: '',
                              attributes: {
                                tooltip: ''
                              },
                            }
                          }
                        ]
                      },
                      {
                        className: 'app-checkbox-group-label',
                        template: '<div>Inne rda dochodu:</div>',
                        hideExpression: 'model.incomeType !== "otherIncome"',
                      },
                      {
                        className: 'app-checkbox-group-list',
                        hideExpression: ($viewValue, $modelValue, scope) => {
                          const hide = $viewValue.incomeType !== 'otherIncome';
                          if (hide) {
                            $viewValue.otherIncomeTypePension = false;
                            $viewValue.otherIncomeTypeAnnuity = false;
                            $viewValue.otherIncomeTypeSeafarer = false;
                            $viewValue.otherIncomeTypeRentalAgreement = false;
                            $viewValue.otherIncomeType500plus = false;
                            $viewValue.otherIncomeTypeSupervisoryBoardMember = false;
                          }
                          return hide;
                        },
                        fieldGroup: [
                          {
                            className: 'app-checkbox',
                            key: 'otherIncomeTypePension',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'emerytura / wiadczenie przedemerytalne (krajowa)',
                              description: '',
                              attributes: {
                                tooltip: ''
                              },
                            }
                          },
                          {
                            className: 'app-checkbox',
                            key: 'otherIncomeTypeAnnuity',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'renta na czas nieokrelony (krajowa)',
                              description: '',
                              attributes: {
                                tooltip: 'Akceptujemy dochd z renty przyznanej na czas nieokrelony, lub jeli osigniesz wiek emerytalny przed wyganiciem renty '
                              },
                            }
                          },
                          {
                            className: 'app-checkbox',
                            key: 'otherIncomeTypeSeafarer',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'marynarz',
                              description: '',
                              attributes: {
                                tooltip: 'Akceptujemy dochd jeli uzyskujesz go od minimum 12 miesicy, oraz  przerwy w zamustrowaniu nie przekraczaj 3 miesicy'
                              },
                            }
                          },
                          {
                            className: 'app-checkbox',
                            key: 'otherIncomeTypeRentalAgreement',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'najem (bez dziaalnoci)',
                              description: '',
                              attributes: {
                                tooltip: 'Akceptujemy dochd jeli uzyskujesz go od minimum 12 miesicy, oraz wynajmowana nieruchomo naley wycznie do Ciebie'
                              },
                            }
                          },
                          {
                            className: 'app-checkbox',
                            key: 'otherIncomeType500plus',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'wiadczenie 500+',
                              description: '',
                              attributes: {
                                tooltip: 'Przyjmiemy wiadczenie 500+ jeli okres kredytowania nie przekracza okresu uzyskiwania wiadczenia'
                              },
                            }
                          },
                          {
                            className: 'app-checkbox',
                            key: 'otherIncomeTypeSupervisoryBoardMember',
                            type: 'checkbox-with-tooltip',
                            defaultValue: false,
                            templateOptions: {
                              label: 'Czonek Rady Nadzorczej',
                              description: '',
                              attributes: {
                                tooltip: 'Akceptujemy dochd jeli uzyskujesz go od minimum 12 miesicy, oraz do koca kadencji pozostao wicej ni 6 miesicy '
                              },
                            }
                          }
                        ]
                      }
                    ]
                  }
                },
                {
                  className: '',
                  template: '<hr />'
                }

              ]
            }
          }
        ]
      }
    ];
  }

  formFieldStepFour(): FormlyFieldConfig[] {
    return [
      {
        fieldGroup: [
          this.formFiledDocumentsList()
        ]
      }
    ];
  }

  private formFiledCreditAmount(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center',
      type: 'digit-input',
      wrappers: ['form-field-horizontal'],
      key: 'CheckListData.loanAmount',
      templateOptions: {
        type: 'number',
        label: 'Chc wnioskowa o kredyt hipoteczny w wysokoci (z):',
        placeholder: 'z',
        description: '',
        attributes: {
          tooltip: ''
        },
        required: true,
        min: 50000,
        max: 4000000,
        change: (field, $event) => {
        }
      }
    };
  }

  private formCreditPurpose(): FormlyFieldConfig {
    return {
      className: 'col-12',
      wrappers: ['form-field-vertical'],
      type: 'custom-radio',
      key: 'creditPurpose',
      templateOptions: {
        required: true,
        label: 'Potrzebuj kredyt na',
        options: [
          { value: 'flat', label: 'zakup mieszkania', tooltip: '', scrollBy: 120},
          { value: 'house', label: 'zakup domu', tooltip: '', scrollBy: 120 },
          { value: 'renovation', label: 'remont / modernizacj domu / mieszkania', tooltip: '' },
          { value: 'building', label: 'budow / rozbudow / dokoczenie budowy domu', tooltip: '' },
          { value: 'refinancing', label: 'refinansowanie', tooltip: '', scrollBy: 120 },
          { value: 'plotPurchase', label: 'zakup dziaki budowlanej / rolnej', tooltip: '' },
        ],
        description: '',
        attributes: {
          tooltip: ''
        },

      },
    };
  }

  private formFlatPurchaseType(): FormlyFieldConfig {
    return {
      className: 'col-12',
      wrappers: ['form-field-vertical'],
      type: 'custom-radio',
      key: 'flatPurchaseType',
      templateOptions: {
        required: true,
        label: 'Kupuj nieruchomo od',
        options: [
          { value: 'developer', label: 'developera / spdzielni', tooltip: '', scrollBy: 120 },
          { value: 'privatePerson', label: 'innej osoby prywatnej', tooltip: '' },
          { value: 'cooperative', label: 'innej osoby prywatnej (spdzielcze wasnociowe prawo do lokalu)', tooltip: '' },
        ],
        description: '',
        attributes: {
          tooltip: ''
        }
      },
      hideExpression: ($viewValue, $modelValue, scope) => {
        const hide = $viewValue.creditPurpose !== 'flat';
        if (hide) {
          $viewValue.flatPurchaseType = undefined;
        }
        return hide;
      }
    };
  }

  private formHousePurchaseType(): FormlyFieldConfig {
    return {
      className: 'col-12',
      wrappers: ['form-field-vertical'],
      type: 'custom-radio',
      key: 'housePurchaseType',
      templateOptions: {
        required: true,
        label: 'Kupuj nieruchomo od',
        options: [
          { value: 'developer', label: 'developera', tooltip: '', scrollBy: 120 },
          { value: 'privatePerson', label: 'innej osoby prywatnej', tooltip: '' },
        ],
        description: '',
        attributes: {
          tooltip: ''
        }
      },
      hideExpression: ($viewValue, $modelValue, scope) => {
        const hide = $viewValue.creditPurpose !== 'house';
        if (hide) {
          $viewValue.housePurchaseType = undefined;
        }
        return hide;
      }
    };
  }

  private formPropertyState(): FormlyFieldConfig {
    return {
      className: 'col-12',
      wrappers: ['form-field-vertical'],
      type: 'custom-radio',
      key: 'propertyState',
      templateOptions: {
        required: true,
        label: 'Wybrana nieruchomo',
        options: [
          { value: 'inUsed', label: 'jest oddana do uytkowania', tooltip: '' },
          { value: 'notInUsed', label: 'nie jest oddana do uytkowania', tooltip: '' },
        ],
        description: '',
        attributes: {
          tooltip: ''
        }
      },
      hideExpression: ($viewValue, $modelValue, scope) => {
        const hide = $viewValue.flatPurchaseType !== 'developer' && $viewValue.housePurchaseType !== 'developer';

        if (hide) {
          $viewValue.propertyState = undefined;
        }
        return hide;
      }
    };
  }

  private formFiledDocumentsList(): FormlyFieldConfig {
    return {
      className: 'col-12 app-justify-content-center',
      wrappers: ['section-info'],
      templateOptions: {
        header: 'Lista dokumentw do kredytu hipotecznego',
        info:
          'odpowiedz na kilka pyta, dziki ktrym przygotujemy list dokumentw, ' +
          'ktrych bdziesz potrzebowa do kredytu mieszkaniowego',
        img: '',
      }
    };
  }

  private formRefinancingType(): FormlyFieldConfig {
    return {
      className: 'col-12',
      wrappers: ['form-field-vertical'],
      type: 'custom-radio',
      key: 'refinancingType',
      templateOptions: {
        required: true,
        label: 'Zabezpieczeniem kredytu bdzie',
        options: [
          { value: 'house', label: 'dom', tooltip: '' },
          { value: 'flat', label: 'mieszkanie', tooltip: '' },
          { value: 'plot', label: 'dziaka budowlana / rolna', tooltip: '' },
          { value: 'cooperative', label: 'spdzielcze wasnociowe prawo do lokalu', tooltip: '' },
        ],
        description: '',
        attributes: {
          tooltip: ''
        },
        groupName: 'refinancingType'
      },
      hideExpression: ($viewValue, $modelValue, scope) => {
        const hide = $viewValue.creditPurpose !== 'refinancing';
        if (hide) {
          $viewValue.refinancingType = undefined;
        }
        return hide;
      }
    };
  }

  private formRefinancingPurpose(): FormlyFieldConfig {
    return {
      className: 'col-12',
      wrappers: ['form-field-vertical'],
      type: 'custom-radio',
      key: 'refinancingPurpose',
      templateOptions: {
        required: true,
        label: 'Chc zrefinansowa',
        options: [
          { value: 'credit', label: 'inny kredyt', tooltip: 'moesz zrefinansowa jednoczenie kilka kredytw', scrollBy: 120 },
          {
            value: 'expenditure',
            label: 'poniesione nakady',
            tooltip: 'np. na zakup nieruchomoci / remontu / budowy domu',
            scrollBy: 120
          },
        ],
        description: '',
        attributes: {
          tooltip: ''
        }
      },
      hideExpression: ($viewValue, $modelValue, scope) => {
        const hide = $viewValue.creditPurpose !== 'refinancing';
        if (hide) {
          $viewValue.refinancingPurpose = undefined;
        }
        return hide;
      }
    }
      ;
  }
}
